'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';

export default function BirthdayQuizPage() {
  const [showReward, setShowReward] = useState(false);
  const [q1, setQ1] = useState('');
  const [q2, setQ2] = useState('');

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    const correctAnswers = {
      q1: "โลตัส",
      q2: "7/11/2544"
    };

    if (q1.trim().toLowerCase() === correctAnswers.q1.toLowerCase() && q2.trim() === correctAnswers.q2) {
      setShowReward(true);
    } else {
      alert('อุ๊ย! คำตอบยังไม่ถูกนะ ลองนึกดีๆ อีกทีสิ!');
    }
  };

  if (showReward) {
    return (
      <div className="flex justify-center items-center min-h-screen bg-gradient-to-r from-rose-300 to-orange-200 p-4 font-body">
        <Card className="w-full max-w-3xl text-center shadow-lg">
          <CardHeader>
            <CardTitle className="text-3xl">เก่งที่สุดเลย! นี่คือรางวัลของเธอ! 🎁</CardTitle>
          </CardHeader>
          <CardContent>
            <p>เพราะเธอคือคนสำคัญ เค้าเลยตั้งใจทำวิดีโอนี้ให้เป็นพิเศษ</p>

            <div className="relative aspect-video my-4 rounded-lg overflow-hidden">
              <iframe
                loading="lazy"
                className="absolute top-0 left-0 w-full h-full"
                src="https://www.canva.com/design/DAGzeXtovDg/-Afq44QSEd3c1EM0eLCq4w/watch?embed"
                allowFullScreen
                title="Surprise Video"
              ></iframe>
            </div>

            <div className="mt-6 p-4 bg-blue-100 rounded-lg border-2 border-dashed border-blue-500">
              <p><strong>นี่คือของขวัญชิ้นแรกสำหรับปีนี้ ของขวัญชิ้นที่2ลองเข้าดูที่ๆเราเปิดทุกวัน</strong></p>
            </div>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="flex justify-center items-center min-h-screen bg-gradient-to-r from-rose-300 to-orange-200 p-4 font-body">
      <Card className="w-full max-w-3xl text-center shadow-lg">
        <CardHeader>
          <CardTitle className="text-3xl">สุขสันต์วันเกิดนะ! 🎂</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="mb-6">มาพิชิตคำถามทายใจ เพื่อปลดล็อกรางวัลสุดพิเศษกัน!</p>
          <form onSubmit={handleSubmit} className="space-y-6 text-left">
            <div className="space-y-2">
              <Label htmlFor="q1" className="text-lg">เราเจอกันครั้งแรกที่ไหน?</Label>
              <Input
                type="text"
                id="q1"
                value={q1}
                onChange={(e) => setQ1(e.target.value)}
                required
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="q2" className="text-lg">วันเกิดของน้องวันที่เท่าไหร่?</Label>
              <Input
                type="text"
                id="q2"
                placeholder="ตัวอย่าง: 31/12/2550"
                value={q2}
                onChange={(e) => setQ2(e.target.value)}
                required
              />
            </div>
            <div className="text-center">
              <Button type="submit" size="lg" className="bg-green-600 hover:bg-green-700">
                ส่งคำตอบ!
              </Button>
            </div>
          </form>
        </CardContent>
      </Card>
    </div>
  );
}
